.header-text {
  font-weight: 600;
  font-size: 3rem;
  text-align: center;
  color: rgb(213, 218, 230);
}

.filter-label {
  font-weight: 600;
  font-size: 1rem;
  color: rgb(213, 218, 230);
  margin-right: 5px;
  min-width: 50px;
  margin-left: 2rem;
}

.filter-select {
  padding: 8px;
  border-radius: 4px;
  border: 1px solid #ccc;
  min-width: 100px;
  font-size: 14px;
  margin-left: 0.25rem;
}

.clear-button {
  padding: 6px 12px;
  background-color: #bdb8b8;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.clear-button:hover {
  background-color: #be5151;
  transform: scale(1.05);
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

/* Global reset & font */
body {
  font-family: 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
  background-color: #555d68;
  margin: 0;
  padding: 0;
  color: #222;
}

h1 {
  text-align: center;
  font-size: 2.2rem;
  font-weight: 700;
  margin: 1.5rem 0;
  color: #1e293b;
}

/* Container for the rankings list */
.rankings-list {
  max-width: 900px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Player card */
.player-card {
  display: flex;
  align-items: center;
  background: rgb(213, 218, 230);
  margin: 0.75rem 0;
  padding: 1rem;
  border-radius: 14px;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.06);
  transition: transform 0.15s ease, box-shadow 0.15s ease;
}

.player-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.08);
}

/* Rank number */
.player-rank {
  font-size: 1.4rem;
  font-weight: bold;
  color: #475569;
  margin-right: 1rem;
}

/* Player image */
.player-image {
  border-radius: 50%;
  width: 56px;
  height: 56px;
  object-fit: cover;
  margin-right: 1rem;
  border: 2px solid #e2e8f0;
}

/* Player info */
.player-info {
  flex: 1;
}

.player-name {
  font-size: 1.2rem;
  font-weight: 600;
  color: #0f172a;
}

.player-meta {
  font-size: 0.9rem;
  color: #64748b;
}

/* Platform rankings container */
.platform-rankings {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
}

.platform-box {
  background: #f1f5f9;
  padding: 0.4rem 0.6rem;
  border-radius: 8px;
  font-size: 0.8rem;
  min-width: 70px;
  text-align: center;
  font-weight: 500;
}

/* Win / loss indicators */
.platform-box.win {
  background-color: #dcfce7;
  color: #166534;
}

.platform-box.loss {
  background-color: #fee2e2;
  color: #991b1b;
}

